/** react */
import React from "react";
import { useHistory } from "react-router-dom";

/** services */
import PATH from "../../constants/path";

/** styles */
import "./city-list.css";
import CloseIm from "../../assets/images/close.svg";

/** types */
import { CurrentWeather } from "../../types";

type Props = {
  label: string | JSX.Element;
  cities: CurrentWeather[];
  onItemRemove: (city: string) => void;
};

const CityList = (props: Props) => {
  const { label, cities, onItemRemove } = props;

  let history = useHistory();

  const cardClick = (city: CurrentWeather) => {
    history.push({
      pathname: `${PATH.BASE}${PATH.DETAIL}/${city.location.name}`,
      state: city,
    });
  };

  const onCardRemove = (
    event: React.MouseEvent<HTMLImageElement, MouseEvent>,
    name: string
  ) => {
    event.stopPropagation();
    onItemRemove(name);
  };

  return (
    <div style={{ margin: "24px 0" }}>
      <div className="city-list-header-ctn">{label}</div>

      <div className="city-list">
        {cities && cities.length > 0 ? (
          cities.map((city, idx) => (
            // CITY CARD
            <div
              key={idx}
              className={"city-card"}
              onClick={() => cardClick(city)}
            >
              <div className={"city-card-content"}>
                <div style={{ flex: 1 }}>
                  {city.location.name}, <br />
                  {city.location.country}
                </div>
                <img
                  src={city.current.condition.icon}
                  alt="weather-icon"
                  className={"city-weather-icon"}
                />
                <span style={{ fontSize: 36, width: 80 }}>
                  {city.current.temp_c.toFixed(0)}&deg;C
                </span>
              </div>

              <img
                src={CloseIm}
                alt="close"
                className="city-remove"
                onClick={(event) => onCardRemove(event, city.location.name)}
              />
            </div>
          ))
        ) : (
          <p style={{ opacity: 0.5, margin: "0 0 24px 24px" }}>Empty list</p>
        )}
      </div>
    </div>
  );
};

export default CityList;
