/** react */
import React, { useState, useEffect } from "react";

/** styles */
import "./city-detail.css";

type Props = {
  note?: string;
  onSave: (note?: string) => void;
  editing: boolean;
};

const Note = (props: Props) => {
  const { note, onSave, editing } = props;

  const [value, setValue] = useState(note);

  useEffect(() => {
    setValue(note);
  }, [note]);

  const updateValue = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    setValue(e.target.value);
  };

  const onSaveValue = () => {
    onSave(value);
  };

  const onRemove = () => {
    onSave("");
    setValue("");
  };

  return (
    <div style={{ display: "flex", alignItems: "flex-end" }}>
      {!editing && !value ? null : (
        <textarea
          placeholder="your note here..."
          className="text-area"
          rows={3}
          cols={50}
          onChange={updateValue}
          disabled={!editing}
        >
          {value}
        </textarea>
      )}
      {editing && (
        <div>
          <button className="button" onClick={onRemove}>
            Remove
          </button>
          <button className="button" onClick={onSaveValue}>
            Save
          </button>
        </div>
      )}
    </div>
  );
};
export default Note;
