/** react */
import React, { useState, useEffect } from "react";
import { StaticContext } from "react-router";
import { RouteComponentProps, useParams } from "react-router-dom";

/** ui */
import Note from "./Note";

/** services */
import services from "../../services";

/** styles */
import "./city-detail.css";
import NoteIm from "../../assets/images/note.png";
import LikeIm from "../../assets/images/like.svg";
import LikedIm from "../../assets/images/liked.svg";

/** types */
import { CurrentWeather } from "../../types";

const CityDetail = (
  props: RouteComponentProps<{}, StaticContext, CurrentWeather>
) => {
  let { name } = useParams<{ name: string }>();
  const { location } = props;
  const { state } = location;

  const [city, setCity] = useState<CurrentWeather | null>(state);
  const [editingNote, setEditingNote] = useState(city?.note === undefined);

  useEffect(() => {
    setCity(state);
  }, [state]);

  useEffect(() => {
    if (!city && name) {
      services.getCurrentByCity(name).then((res: any) => {
        if (res && res.sttus === 200) {
          if (res.data && res.data.current) {
            const city = res.data as CurrentWeather;

            setCity(services.appendFavoriteInfoNSaveLocal(city, name));

            if (name !== city.location.name)
              services.saveCurrentByCity(res.data);
          } else {
            setCity(null);
          }
        }
      });
    }
  }, [city, name]);

  const toggleLike = () => {
    if (city) {
      city.like
        ? services.removeFavorite(city.location.name)
        : services.addFavorite(city.location.name);

      setCity({ ...city, like: !city.like });
    }
  };

  const toggleEditNote = () => {
    if (!editingNote) setEditingNote(!editingNote);
  };
  const saveNote = (note?: string) => {
    if (city) {
      const newCity = { ...city, note };
      setCity(newCity);
      services.saveCurrentByCity(newCity);

      setEditingNote(false);
    }
  };

  const genRow = (
    label: string,
    value: string | number,
    unit: string | JSX.Element = ""
  ) => {
    return (
      <div className={"city-detail-row"}>
        <div style={{ opacity: 0.8 }}>{label}</div>
        <div style={{ fontSize: 18, fontWeight: 500 }}>
          {value}
          {unit}
        </div>
      </div>
    );
  };

  if (!city) return <div>No Result</div>;

  return city ? (
    <div>
      <div className="detail-header-ctn">
        <h2>
          {city.location.name}, {city.location.country}
        </h2>
        <div style={{ display: "flex", alignItems: "center" }}>
          <img
            src={NoteIm}
            alt="note"
            className={"detail-action-icon"}
            onClick={toggleEditNote}
          />
          <img
            src={city.like ? LikedIm : LikeIm}
            alt="like"
            className={"detail-action-icon"}
            onClick={toggleLike}
          />
        </div>
      </div>
      <Note note={city.note} onSave={saveNote} editing={editingNote} />

      <p style={{ fontSize: 18, fontWeight: 500, marginBottom: 0 }}>
        CURRENT WEATHER
      </p>
      <p className={"local-time"}>{city.location.localtime}</p>
      <div
        style={{ display: "flex", alignItems: "center", position: "relative" }}
      >
        <div className="city-detail-icon-ctn">
          <img
            src={city.current.condition.icon}
            alt="weather-icon"
            width={150}
          />
          <p style={{ marginTop: 8, textAlign: "center" }}>
            {city.current.condition.text}
          </p>
        </div>

        {/* temperature */}
        <div style={{ marginLeft: 16 }}>
          <div className="city-detail-temp">
            {city.current.temp_c}&deg;
            <span style={{ fontSize: 24, marginLeft: -25, opacity: 0.6 }}>
              C
            </span>
          </div>
          <div className={"city-detail-feellike"}>
            Feel Like {city.current.feelslike_c}&deg;
          </div>
        </div>
      </div>

      <div className="city-detail-rows-ctn">
        {genRow("Wind", city.current.wind_kph, "km/h")}
        {genRow("Wind Degree", city.current.wind_degree, <>&deg;C</>)}
        {genRow("Wind Direction", city.current.wind_dir)}

        {genRow("Pressure", city.current.pressure_mb, "mb")}
        {genRow("Humidity", city.current.humidity, "%")}
        {genRow("Cloud Cover", city.current.cloud, "%")}
        {genRow("UV Index", city.current.uv)}
        {genRow("Visibility", city.current.vis_km, "km")}
      </div>
    </div>
  ) : (
    <div>No result</div>
  );
};

export default CityDetail;
