/** react */
import React, { useState, useEffect } from "react";
import { NavLink } from "react-router-dom";
import { useHistory } from "react-router-dom";

/** ui */
import SearchBar from "../../components/SearchBar";
import PATH from "../../constants/path";

/** services */
import services from "../../services";

/** styles */
import "./header.css";

const Header = () => {
  const [searchText, setSearchText] = useState<string>();

  let history = useHistory();

  useEffect(() => {
    const successCb = (position: GeolocationPosition) => {
      services
        .getCurrentByCity(
          `${position.coords.latitude}, ${position.coords.longitude}`
        )
        .then(handleSearchRes);
    };

    if (window.navigator.geolocation) {
      window.navigator.geolocation.getCurrentPosition(successCb);
    }
    // eslint-disable-next-line
  }, []);

  const handleSearchRes = (res: any) => {
    if (res && res.status === 200 && res.data && res.data.current) {
      history.push({
        pathname: `${PATH.BASE}${PATH.DETAIL}/${res.data.location.name}`,
        state: services.appendFavoriteInfoNSaveLocal(res.data),
      });
    } else {
      history.push({
        pathname: `${PATH.BASE}${PATH.DETAIL}/${searchText}`,
        state: null,
      });
    }
  };

  const search = () => {
    if (searchText) {
      services.getCurrentByCity(searchText).then(handleSearchRes);
    }
  };

  return (
    <header className="header-ctn">
      <div className="header-content">
        <div style={{ display: "flex", height: "100%" }}>
          <NavLink
            to="/"
            exact
            activeClassName="header-active"
            className="header-menu"
          >
            Home
          </NavLink>

          <NavLink
            activeClassName="header-active"
            className="header-menu"
            style={{ marginLeft: 20 }}
            to="/detail"
          >
            Detail Page
          </NavLink>
        </div>
        <SearchBar
          searchText={searchText}
          setSearchText={setSearchText}
          onSearch={search}
        />
      </div>
    </header>
  );
};

export default Header;
