/** react */
import React, { useState, useEffect } from "react";

/** component */
import CityList from "./CityList";

/** services */
import services from "../../services";

/** styles */
import LikedIm from "../../assets/images/liked.svg";

/** types */
import { CurrentWeather } from "../../types";

const Home = () => {
  const [topCities, setTopCities] = useState<CurrentWeather[]>([]);
  const [favCities, setFavCities] = useState<CurrentWeather[]>([]);

  useEffect(() => {
    const topCities = services.getTopCities();

    topCities.forEach((city) => {
      if (city) {
        services.getCurrentByCity(city).then((res: any) => {
          if (res && res.status === 200) {
            if (res.data && res.data.current) {
              setTopCities((cities) => [
                ...cities,
                services.appendFavoriteInfoNSaveLocal(res.data),
              ]);
            }
          }
        });
      }
    });

    let favCities: string[] = services.getFavorites();

    favCities.forEach((city) => {
      services.getCurrentByCity(city).then((res: any) => {
        setFavCities((cities) => [...cities, { ...res.data, like: true }]);

        services.saveCurrentByCity(res.data);
      });
    });
  }, []);

  const onRemoveFav = (name: string) => {
    services.removeFavorite(name);
    setFavCities((cities) => cities.filter((c) => c.location.name !== name));
  };
  const onRemoveTopCity = (name: string) => {
    services.removeTopCity(name);
    setTopCities((cities) => cities.filter((c) => c.location.name !== name));
  };

  return (
    <div>
      <CityList
        label={
          <>
            Favorites
            <img
              src={LikedIm}
              alt="like"
              style={{ width: 24, marginLeft: 16 }}
            />
          </>
        }
        cities={favCities}
        onItemRemove={onRemoveFav}
      />

      <CityList
        label="Top Cities"
        cities={topCities}
        onItemRemove={onRemoveTopCity}
      />
    </div>
  );
};

export default Home;
