import axios from "axios";

const BASE = "https://api.weatherapi.com/v1/";

const instance = axios.create({ baseURL: BASE, timeout: 3000 });
instance.defaults.params = {};
instance.defaults.params["key"] = "41a3e4d0c8414773969125202200812"; // TODO:

const get = (url: string) => {
  return instance.get(url);
};

export default { get };
