import restClient from "./restClient";

import { CurrentWeather, CurrentWeatherRes } from "../types";

import { TOP15 } from "./constants";

/** localStorage key */
const FAVORITES = "FAVORITES";
const TOP_CITIES = "TOP_CITIES";
const SEPARATOR = "|";

/** ENDPOINT */
const BASE = "/current.json";
const GET_CURRENT = (city: string) => `${BASE}?q=${city}`;

// TOP CITIES
const getTopCities = (): string[] => {
  let topCitiesStr = localStorage.getItem(TOP_CITIES);
  let topCities: string[];

  if (topCitiesStr === null) {
    topCities = TOP15.sort((a, b) => a.localeCompare(b));
    localStorage.setItem(TOP_CITIES, topCities.join(SEPARATOR));
  } else {
    topCities = topCitiesStr.split(SEPARATOR);
  }

  return topCities;
};

const getCurrentByCity = (city: string) => {
  const data = localStorage.getItem(city);

  if (data)
    return new Promise((resolve) => {
      resolve({ status: 200, data: JSON.parse(data) });
    });

  return restClient.get(GET_CURRENT(city));
};

const saveCurrentByCity = (weather: CurrentWeather, city?: string) => {
  if (city) localStorage.setItem(city, JSON.stringify(weather));
  else localStorage.setItem(weather.location.name, JSON.stringify(weather));
};

const removeTopCity = (name: string) => {
  const topCitiesStr = localStorage.getItem(TOP_CITIES);

  if (topCitiesStr) {
    let topCities: string[] = topCitiesStr.split(SEPARATOR);

    const idx = topCities.indexOf(name);
    if (idx !== -1) {
      topCities.splice(idx, 1);
      localStorage.setItem(TOP_CITIES, topCities.join(SEPARATOR));
    }
  }
};

// FAVORITES
const getFavorites = () => {
  const favStr = localStorage.getItem(FAVORITES);
  let favs: string[];

  if (favStr) {
    favs = favStr.split("|");
  } else {
    favs = [];
    localStorage.setItem(FAVORITES, "");
  }

  return favs.sort((a, b) => a.localeCompare(b));
};

const addFavorite = (city: string) => {
  const favStr = localStorage.getItem(FAVORITES);
  let favs: string[];

  if (favStr) {
    favs = favStr.split(SEPARATOR);
  } else favs = [];

  favs.push(city);

  localStorage.setItem(FAVORITES, favs.join(SEPARATOR));
};

const removeFavorite = (name: string) => {
  const favStr = localStorage.getItem(FAVORITES);

  if (favStr) {
    let favs: string[] = favStr.split(SEPARATOR);

    const idx = favs.indexOf(name);
    if (idx !== -1) {
      favs.splice(idx, 1);
      localStorage.setItem(FAVORITES, favs.join(SEPARATOR));
    }
  }
};

const appendFavoriteInfoNSaveLocal = (
  weather: CurrentWeatherRes,
  name?: string
): CurrentWeather => {
  saveCurrentByCity(weather, name);
  return {
    ...weather,
    like: getFavorites().indexOf(weather.location.name) !== -1,
  };
};

export default {
  getTopCities,
  getCurrentByCity,
  saveCurrentByCity,
  removeTopCity,
  getFavorites,
  addFavorite,
  removeFavorite,
  appendFavoriteInfoNSaveLocal,
};
